import { getFetchId, stateKey } from './userAccessCommon';
export const selectUserAccessState = (state) => state[stateKey];
// Fetch userAccess
export const selectUserAccess = (state, providerType, providerQueryString) => selectUserAccessState(state).byId.get(getFetchId(providerType, providerQueryString));
export const selectUserAccessFetchStatus = (state, userAccessType, userAccessQueryString) => selectUserAccessState(state).fetchStatus.get(getFetchId(userAccessType, userAccessQueryString));
export const selectUserAccessError = (state, userAccessType, userAccessQueryString) => selectUserAccessState(state).errors.get(getFetchId(userAccessType, userAccessQueryString));
