import { FetchStatus } from 'store/common';
import { resetState } from 'store/ui/uiActions';
import { getType } from 'typesafe-actions';
import { fetchOrgFailure, fetchOrgRequest, fetchOrgSuccess } from './orgActions';
const defaultState = {
    byId: new Map(),
    fetchStatus: new Map(),
    errors: new Map(),
};
export function orgReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(resetState):
            state = defaultState;
            return state;
        case getType(fetchOrgRequest):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.payload.fetchId, FetchStatus.inProgress) });
        case getType(fetchOrgSuccess):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.fetchId, FetchStatus.complete), byId: new Map(state.byId).set(action.meta.fetchId, Object.assign(Object.assign({}, action.payload), { timeRequested: Date.now() })), errors: new Map(state.errors).set(action.meta.fetchId, null) });
        case getType(fetchOrgFailure):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.fetchId, FetchStatus.complete), errors: new Map(state.errors).set(action.meta.fetchId, action.payload) });
        default:
            return state;
    }
}
