import { stateKey } from './uiReducer';
export const selectUIState = (state) => state[stateKey];
export const selectIsExportsDrawerOpen = (state) => selectUIState(state).isExportsDrawerOpen;
export const selectIsOptimizationsDrawerOpen = (state) => selectUIState(state).isOptimizationsDrawerOpen;
export const selectOptimizationsDrawerPayload = (state) => selectUIState(state).payload;
