import { exportStateKey, getFetchId } from './exportCommon';
export const selectExportState = (state) => state[exportStateKey];
export const selectExport = (state, reportPathsType, reportType, reportQueryString) => selectExportState(state).byId.get(getFetchId(reportPathsType, reportType, reportQueryString));
export const selectExportFetchStatus = (state, reportPathsType, reportType, reportQueryString) => selectExportState(state).fetchStatus.get(getFetchId(reportPathsType, reportType, reportQueryString));
export const selectExportError = (state, reportPathsType, reportType, reportQueryString) => selectExportState(state).errors.get(getFetchId(reportPathsType, reportType, reportQueryString));
