import { getFetchId, orgStateKey } from './orgCommon';
export const selectOrgState = (state) => state[orgStateKey];
export const selectOrg = (state, orgPathsType, orgType, orgQueryString) => selectOrgState(state).byId.get(getFetchId(orgPathsType, orgType, orgQueryString));
export const selectOrgFetchStatus = (state, orgPathsType, orgType, orgQueryString) => selectOrgState(state).fetchStatus.get(getFetchId(orgPathsType, orgType, orgQueryString));
export const selectOrgError = (state, orgPathsType, orgType, orgQueryString) => selectOrgState(state).errors.get(getFetchId(orgPathsType, orgType, orgQueryString));
