import { stateKey } from './featureFlagsReducer';
export const selectFeatureFlagsState = (state) => state[stateKey];
export const selectHasFeatureFlags = (state) => selectFeatureFlagsState(state).hasFeatureFlags;
export const selectIsExportsFeatureEnabled = (state) => selectFeatureFlagsState(state).isExportsFeatureEnabled;
export const selectIsFinsightsFeatureEnabled = (state) => selectFeatureFlagsState(state).isFinsightsFeatureEnabled;
export const selectIsIbmFeatureEnabled = (state) => selectFeatureFlagsState(state).isIbmFeatureEnabled;
export const selectIsRosFeatureEnabled = (state) => selectFeatureFlagsState(state).isRosFeatureEnabled;
