import { getFetchId, tagStateKey } from './tagCommon';
export const selectTagState = (state) => state[tagStateKey];
export const selectTag = (state, tagPathsType, tagType, tagQueryString) => selectTagState(state).byId.get(getFetchId(tagPathsType, tagType, tagQueryString));
export const selectTagFetchStatus = (state, tagPathsType, tagType, tagQueryString) => selectTagState(state).fetchStatus.get(getFetchId(tagPathsType, tagType, tagQueryString));
export const selectTagError = (state, tagPathsType, tagType, tagQueryString) => selectTagState(state).errors.get(getFetchId(tagPathsType, tagType, tagQueryString));
