import { getCurrency } from 'utils/localStorage';
import { getQueryForWidget, getQueryForWidgetTabs, rhelDashboardDefaultFilters, rhelDashboardStateKey, rhelDashboardTabFilters, } from './rhelDashboardCommon';
export const selectRhelDashboardState = (state) => state[rhelDashboardStateKey];
export const selectWidgets = (state) => selectRhelDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectRhelDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const defaultFilter = Object.assign(Object.assign({}, rhelDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, rhelDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    const props = {
        currency: getCurrency(),
    };
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, defaultFilter), { time_scope_value: -2 }), props),
        current: getQueryForWidget(defaultFilter, props),
        forecast: getQueryForWidget({}, Object.assign({ limit: 31 }, props)),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' }), props),
    };
};
