import { rhelCostOverviewStateKey } from './rhelCostOverviewCommon';
export const selectRhelCostOverviewState = (state) => state[rhelCostOverviewStateKey];
export const selectWidgets = (state) => selectRhelCostOverviewState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectRhelCostOverviewState(state).currentWidgets;
