    for (let i = padDate.getDate(); i < firstDate.getDate(); i++) {
        padDate.setDate(i);
        const id = format(padDate, 'yyyy-MM-dd');
        result.push(createReportDatum(null, { id }, 'date', null));
    }
    // Fill middle with existing data
    result.push(...datums);
