import { getFetchId, rosStateKey } from './rosCommon';
export const selectRosState = (state) => state[rosStateKey];
export const selectRos = (state, rosPathsType, rosType, rosQueryString) => selectRosState(state).byId.get(getFetchId(rosPathsType, rosType, rosQueryString));
export const selectRosFetchStatus = (state, rosPathsType, rosType, rosQueryString) => selectRosState(state).fetchStatus.get(getFetchId(rosPathsType, rosType, rosQueryString));
export const selectRosError = (state, rosPathsType, rosType, rosQueryString) => selectRosState(state).errors.get(getFetchId(rosPathsType, rosType, rosQueryString));
