import { forecastStateKey, getFetchId } from './forecastCommon';
export const selectForecastState = (state) => state[forecastStateKey];
export const selectForecast = (state, forecastPathsType, forecastType, forecastQueryString) => selectForecastState(state).byId.get(getFetchId(forecastPathsType, forecastType, forecastQueryString));
export const selectForecastFetchStatus = (state, forecastPathsType, forecastType, forecastQueryString) => selectForecastState(state).fetchStatus.get(getFetchId(forecastPathsType, forecastType, forecastQueryString));
export const selectForecastError = (state, forecastPathsType, forecastType, forecastQueryString) => selectForecastState(state).errors.get(getFetchId(forecastPathsType, forecastType, forecastQueryString));
