import { addProviderKey, getFetchId, stateKey } from './providersCommon';
export const selectProvidersState = (state) => state[stateKey];
// Add provider
export const selectAddProviderFetchStatus = (state) => selectProvidersState(state).fetchStatus.get(addProviderKey);
export const selectAddProviderError = (state) => selectProvidersState(state).errors.get(addProviderKey);
// Fetch providers
export const selectProviders = (state, providerType, provideQueryString) => selectProvidersState(state).byId.get(getFetchId(providerType, provideQueryString));
export const selectProvidersFetchStatus = (state, providerType, provideQueryString) => selectProvidersState(state).fetchStatus.get(getFetchId(providerType, provideQueryString));
export const selectProvidersError = (state, providerType, provideQueryString) => selectProvidersState(state).errors.get(getFetchId(providerType, provideQueryString));
