import { rhelHistoricalDataStateKey } from './rhelHistoricalDataCommon';
export const selectRhelHistoricalDataState = (state) => state[rhelHistoricalDataStateKey];
export const selectWidgets = (state) => selectRhelHistoricalDataState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectRhelHistoricalDataState(state).currentWidgets;
