import { getType } from 'typesafe-actions';
import { setFeatureFlags } from './featureFlagsActions';
export const defaultState = {
    hasFeatureFlags: false,
    isExportsFeatureEnabled: false,
    isFinsightsFeatureEnabled: false,
    isIbmFeatureEnabled: false,
    isRosFeatureEnabled: false,
};
export const stateKey = 'featureFlags';
export function featureFlagsReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(setFeatureFlags):
            return Object.assign(Object.assign({}, state), { hasFeatureFlags: true, isExportsFeatureEnabled: action.payload.isExportsFeatureEnabled, isFinsightsFeatureEnabled: action.payload.isFinsightsFeatureEnabled, isIbmFeatureEnabled: action.payload.isIbmFeatureEnabled, isRosFeatureEnabled: action.payload.isRosFeatureEnabled });
        default:
            return state;
    }
}
