import { getType } from 'typesafe-actions';
import { closeExportsDrawer, closeOptimizationsDrawer, openExportsDrawer, openOptimizationsDrawer, resetState, } from './uiActions';
export const defaultState = {
    isExportsDrawerOpen: false,
    isOptimizationsDrawerOpen: false,
    payload: undefined,
};
export const stateKey = 'ui';
export function uiReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(resetState):
            state = defaultState;
            return state;
        case getType(closeExportsDrawer):
            return Object.assign(Object.assign({}, state), { isExportsDrawerOpen: false });
        case getType(closeOptimizationsDrawer):
            return Object.assign(Object.assign({}, state), { isOptimizationsDrawerOpen: false });
        case getType(openExportsDrawer):
            return Object.assign(Object.assign({}, state), { isExportsDrawerOpen: true });
        case getType(openOptimizationsDrawer):
            return Object.assign(Object.assign({}, state), { isOptimizationsDrawerOpen: true, payload: Object.assign({}, action.payload) });
        default:
            return state;
    }
}
