import { getFetchId, resourceStateKey } from './resourceCommon';
export const selectResourceState = (state) => state[resourceStateKey];
export const selectResource = (state, resourcePathsType, resourceType, resourceQueryString) => selectResourceState(state).byId.get(getFetchId(resourcePathsType, resourceType, resourceQueryString));
export const selectResourceFetchStatus = (state, resourcePathsType, resourceType, resourceQueryString) => selectResourceState(state).fetchStatus.get(getFetchId(resourcePathsType, resourceType, resourceQueryString));
export const selectResourceError = (state, resourcePathsType, resourceType, resourceQueryString) => selectResourceState(state).errors.get(getFetchId(resourcePathsType, resourceType, resourceQueryString));
